export const INPUT_SCANF_TITLE = "inputScanfTitle"
export const SAVE_SCANF_FONT_SIZE = "saveScanfFontSize"
export const SAVE_SCANF_COLOR = "saveScanfColor"
export function inputScanfTitle(text) {
    return { type: INPUT_SCANF_TITLE, payload: text }
}

export function saveScanfFontSize(size) {
    return { type: SAVE_SCANF_FONT_SIZE, payload: size }
}

export function saveScanfColor(colorTextEvent) {
    const metaColor = colorTextEvent.metaColor
    const rgba = `rgba(${metaColor.r},${metaColor.g},${metaColor.b},${metaColor.a})`;
    return { type: SAVE_SCANF_COLOR, payload: rgba }
}
