import React from "react"
import { connect } from "react-redux"
import { inputScanfTitle, saveScanfColor, saveScanfFontSize } from "../actions/textAction"
import { Button, Row, Col, Select, ColorPicker } from "antd"
import { savePlayerState } from "../actions/playerAction"
class OpLeft extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            fontOptionList: [...new Array(50).fill(0).map((item, index) => {
                let v = item + 12 + index
                return {
                    value: v,
                    label: v
                }
            })],
            speedList: [
                { value: 0.75, label: 0.75 },
                { value: 1, label: 1 },
                { value: 1.25, label: 1.25 },
                { value: 1.5, label: 1.5 },
                { value: 2, label: 2 },
                { value: 2.5, label: 2.5 },
                { value: 3, label: 3 },
                { value: 3.5, label: 3.5 },
                { value: 4, label: 4 },
            ]
        }
    }
    replay = () => {
        this.props.remotionPlayerRef.current.seekTo(0)
        this.props.remotionPlayerRef.current.play()
    }
    play = () => {
        this.props.remotionPlayerRef.current.play()
    }
    pause = () => {
        this.props.remotionPlayerRef.current.pause()
    }
    mute = () => {
        this.props.remotionPlayerRef.current.mute()
    }
    toggleControl = () => {
        const { playerControls, savePlayerState } = this.props
        savePlayerState({
            controls: !playerControls
        })
    }
    toggleSpeed = (speed = 1) => {
        const { savePlayerState } = this.props
        savePlayerState({
            playbackRate: speed
        })
    }

    render = () => {
        const { scanfTitle, inputScanfTitle, saveScanfFontSize, titleFontSize, saveScanfColor, titleColor, playbackRate } = this.props

        return (<>
            <Row>
                <Col span={24}><input value={scanfTitle} onChange={inputScanfTitle} /></Col>
            </Row>
            <Row>
                <Col span={24}><Button onClick={this.play}>paly</Button></Col>
                <Col span={24}><Button onClick={this.pause}>pause</Button></Col>
                <Col span={24}><Button onClick={this.replay}>replay</Button></Col>
                <Col span={24}><Button onClick={this.mute}>mute</Button></Col>
                <Col span={24}><Button onClick={this.toggleControl}></Button></Col>
                <Col span={24}></Col>
                <Col span={24}>
                    <Select
                        defaultValue={playbackRate}
                        onChange={this.toggleSpeed}
                        style={{ width: "150px" }}
                        options={this.state.speedList}
                    >

                    </Select></Col>
            </Row>
            <Row>
                <Col span={24}>: {titleFontSize}px</Col>
                <Col span={24}>
                    <Select
                        defaultValue={titleFontSize}
                        onChange={saveScanfFontSize}
                        style={{ width: "150px" }}
                        options={this.state.fontOptionList}
                    >
                    </Select></Col>
            </Row>
            <Row>
                <Col span={24}></Col>
                <Col span={24}>
                    <ColorPicker value={titleColor} onChange={saveScanfColor}></ColorPicker>
                </Col>
            </Row>
        </>)
    }
}

export default connect((state) => {
    return {
        scanfTitle: state.textReducer.scanfTitle,
        titleFontSize: state.textReducer.titleFontSize,
        titleColor: state.textReducer.titleColor,
        playerControls: state.playerReducer.playerIsState.controls,
        playbackRate: state.playerReducer.playerIsState.playbackRate,
    }
}, (dispatch) => {
    return {
        inputScanfTitle(event) {
            dispatch(inputScanfTitle(event.target.value))
        },
        saveScanfFontSize(size) {
            dispatch(saveScanfFontSize(size))
        },
        saveScanfColor(colorTextEvent) {
            dispatch(saveScanfColor(colorTextEvent))
        },
        savePlayerState(playerState) {
            dispatch(savePlayerState(playerState))
        }
    }
})(OpLeft)