import React, { useRef } from "react"
import { spring, useCurrentFrame, useVideoConfig } from "remotion"
import { useSelector } from "react-redux"
import Draggable from 'react-draggable';
import styles from "./logo.module.css"

function Tip(props) {
    let state = useSelector(state => state.textReducer)
    let tipWrapper = useRef(null)
    let willDrag = useRef(null)
    const frame = useCurrentFrame();
    const { fps } = useVideoConfig();
    const scale = spring({
        fps,
        frame,
    });
    let textStyle = {
        transition: "all 1s",
        transform: `scale(${scale})`,
        color: frame / 2 < 0.5 ? "blue" : "pink",
        fontSize: `${state.titleFontSize}px`,
        color: state.titleColor,
        opacity: 1
    }
    if (frame > 20) {
        textStyle["opacity"] = 0.8
    }
    let tipStyle = {
      
    }
    if (state.scanfTitle) {
        tipStyle["display"] = "block"
    }else{
        tipStyle["display"] = "none"
    }
    return (<>
        {/*  */}
        {<div className={styles.tip} style={tipStyle} ref={tipWrapper} >
            <Draggable bounds={".tip-position"} nodeRef={willDrag}>
                <div className={styles.title} ref={willDrag}><span style={textStyle}>{state.scanfTitle}</span> </div>
            </Draggable>
        </div>}
    </>)
}

function EllipseCom(props) {
    const frame = useCurrentFrame();
    const rotate = 15 * frame
    const ellWrapperStyle = {
        transform: `rotate(${rotate}deg)`
    }
    return (<div className={styles.ellWrapper} style={ellWrapperStyle}>
        <div className={`${styles.ellipse} ${styles.ellipse1}`}></div>
        <div className={`${styles.ellipse} ${styles.ellipse2}`}></div>
        <div className={`${styles.ellipse} ${styles.ellipse3}`}></div>
        <div className={styles.innerBall}></div>
    </div>)
}

function Logo(props) {
    // Logo
    const state = useSelector(state => state.textReducer)
    return (
        <>
            <div className={`${styles.logoWrapper} tip-position`}>
                <EllipseCom />
                <Tip />
            </div>
        </>
    )
}
export default Logo 