import { INPUT_SCANF_TITLE, SAVE_SCANF_FONT_SIZE, SAVE_SCANF_COLOR } from "../actions/textAction"

const initialState = {
    scanfTitle: "Welcome to creatify!",
    titleFontSize: 30,
    titleColor: "#000"
}
export function textReducer(state = initialState, action) {
    switch (action.type) {
        case INPUT_SCANF_TITLE: {
            return {
                ...state,
                scanfTitle: action.payload
            }
        }
        case SAVE_SCANF_FONT_SIZE: {
            return {
                ...state,
                titleFontSize: action.payload
            }
        }
        case SAVE_SCANF_COLOR: {
            return {
                ...state,
                titleColor: action.payload
            }
        }
        default: {
            return state
        }
    }
}