import React, { useEffect, useRef } from "react";
import { useSelector, Provider } from "react-redux"
import { Player } from '@remotion/player';
import "./assets/App.css"
import "antd/dist/reset.css"
import { Col, Row } from 'antd';
import { store } from "./store/store"
import HelloWorld from "./Components/HelloWorld"
import OpLeft from "./Components/OpLeft";

function AppWrapper() {
  return (<Provider store={store}><App /></Provider>)
}

function App() {
  const playerState = useSelector(state => state.playerReducer)
  // 
  const remotionPlayerRef = useRef(null)

  return (

    <div style={{ width: "80%", margin: "0 auto" }}>
      <Row>
        <Col span={8}>
          <OpLeft remotionPlayerRef={remotionPlayerRef}></OpLeft>
        </Col>
        <Col span={16}>
          <Player
            ref={remotionPlayerRef}
            component={HelloWorld}
            durationInFrames={120}
            compositionWidth={800}
            compositionHeight={800}
            fps={30}
            playbackRate={playerState.playerIsState.playbackRate}
            controls={playerState.playerIsState.controls}
          />
        </Col>
      </Row>
    </div>
  );
}
export default AppWrapper;
