import { SAVE_PLAYER_STATE } from "./../actions/playerAction"
// 
const initialState = {
    playerIsState: {
        isPlaying: false,
        controls: true,
        fps: 30,
        playbackRate: 1
    }
}
export function playerReducer(state = initialState, action) {
    console.log("action", action)
    switch (action.type) {
        case SAVE_PLAYER_STATE: {
            return {
                ...state,
                playerIsState: {
                    ...state.playerIsState,
                    ...action.payload
                }
            }
        }
        default: {
            return state
        }
    }
}