 
export const SAVE_PLAYER_STATE = "savePlayerState"

export function savePlayerState(playerState) {
    console.log(playerState,"playerState")
    let payload = {}
    if (Object.prototype.toString.call(playerState) === '[object Object]') {
        payload = {
            ...playerState
        }
    }
    return { type: SAVE_PLAYER_STATE, payload }
}
